import loadErr from '@/components/loadErr.vue'
import noMore from '@/components/noMore.vue'

export default {
	data() {
		return {
			navTitle: '点评',
			commentType: null, //评论类型
			index: 1,
			size: 10,
			commentList: [],
			isLoadErr: false, //页面加载出错
			isMore: true,
			id: null,
			type: '', //
			tagArr: [], //标签
			
			
		}
	},
	components: { loadErr, noMore },
	onLoad(opt) {
		this.id = opt.id
		this.commentType = opt.type
		
		this.getCommentTag()
		this.getCommentData()
	},
	methods: {
		toSelectCommentTag(idx) {
			if(this.type == this.tagArr[idx].tag_id) {
				this.type = ''
			}else {
				this.type = this.tagArr[idx].tag_id
			}

			this.isMore = true
			this.index = 1
			this.commentList = []
			this.getCommentData()
		},
		toPreviewImg(pIdx, cIdx) {
			const $this = this
			uni.previewImage({
				current: $this.commentList[pIdx].imgs[cIdx],
				urls: $this.commentList[pIdx].imgs
			})
		},
		getCommentTag() {
			this.$api.ajax({
				url: this.commentType == 'line'? this.$config.lineCommentTag: '',
				data: {
					rt_id: this.id
				}
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					let arr = res.data.data
					this.tagArr = arr
				}else {
					this.$api.warnNotice(res.data.message)
				}
			})
			.catch((err) => {
				// this.$api.warnNotice('标签加载失败')
			})
		},
		toLoadMore() {
			if(this.isMore) {
				this.getCommentData()
			}
		},
		getCommentData() {
			if(this.index == 1) {
				uni.showLoading({
					title: '数据加载中',
					mask: true
				})
			}
			this.$api.ajax({
				url: this.commentType == 'line'? this.$config.lineComment: '',
				data: {
					index: this.index,
					size: this.size,
					type: this.type,
					rt_id: this.id
				}
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					let arr = res.data.data
					arr.map(v => {
						if(v.picture_arr) {
							v.imgs = v.picture_arr.split(',')
						}else {
							v.imgs = []
						}
					})
					if(arr.length < this.size) {
						this.commentList = this.commentList.concat(arr)
						this.isMore = false //没有更多了
					}else {
						this.commentList = this.commentList.concat(arr)
						this.index++
					}
					setTimeout(() => {
						uni.hideLoading();
					}, 500)
				}else {
					if(this.index == 1) {
						this.isLoadErr = true
					}else {
						this.$api.warnNotice(res.data.message)
					}
					uni.hideLoading();
				}
			})
			.catch((err) => {
				if(this.index == 1) {
					this.isLoadErr = true
				}else {
					this.$api.warnNotice('列表加载失败')
				}
				uni.hideLoading();
			})
		},
		toRelaodData() { //刷新加载
			this.isLoadErr = false
			this.isMore = true
			this.index = 1
			this.commentList = []
			this.getCommentData()
		}
	}
}
